.Footer {
  position: relative;
  z-index: 5;
  padding: 30px 0;
  background: #2c2c2c;
  color: #fff;
}

.Footer__logo img {
  width: 110px;
  padding-top: 3rem;
}

.Footer__logo-container {
  padding-top: 20px;
}

.Footer__section-title {
  color: #ce5a5a;
  font-size: 17px;
}

.Footer__section-list {
  list-style: none;
  padding: 0;
}

.Footer__section-listItem a {
  text-decoration: none;
  color: #d4cbce;
  font-weight: 400;
}

.Footer__section-listItem a:hover {
  color: #ebe7e8;
}

.Footer .copyright h5 {
  color: #ce5a5a;
  font-size: 11px;
}

body {
  /*font-size: 1.9rem;*/
  font-weight: 300;
  font-family: 'Fira Sans', sans-serif;
}

a,
a:hover {
  text-decoration: none !important;
}

.bg-img {
  /* Control the height of the image */
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.bg-img form {
  position: absolute;
  right: 0;
  max-width: 500px;
  padding: 16px;
  background-color: white;
  font-size: 14px;
}

@media (max-width: 768px) {
  .bg-img form {
    margin-top: -40px;
    max-width: 500px;
    padding: 16px;
    background-color: white;
    font-size: 14px;
    border-radius: 15px;
    border: 1px solid #eee;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
  }
}

.help-block {
  font-size: 13px;
  color: red;
}

.roundImage {
  width: 163px;
  height: 163px;
  border-radius: 3px;
  object-fit: cover;
  transform: translateY(-0.5rem);
}

h5.recentUsername {
  font-size: 19px;
  font-weight: 700;
  color: #ce5a5a;
  letter-spacing: 1px;
  margin-top: 0;
  margin-bottom: 0;
}

.warning-box {
  display: flex;
  height: 45px;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.warning-box h6 {
  margin-bottom: -2px;
}

@media (max-width: 400px) {
  .warning-box * {
    font-size: 14px !important;
  }
}

@media (max-width: 340px) {
  .warning-box * {
    font-size: 12px !important;
  }
}

.imgHeader {
  background: url("/img/sites/liefdesdate-nl/lp1-header.jpg") center/cover no-repeat;
  min-height: 226px;
  margin-bottom: 14px;
}

.logo img {
  width: 100px;
}

@media (min-width: 768px) {
  .firstAfterHero {
    margin-top: 30px;
  }

  .bg-img {
    background-image: url("/img/sites/liefdesdate-nl/lp1-header.jpg");
    min-height: 680px;
  }

  .bg-img.imageDontShowLocal {
    background-image: none;
  }

  .bg-img form {
    margin: 20px;
  }

  .logo img {
    width: 180px;
  }

  .loginForm {
    width: 500px;
  }
}

@media (max-width: 768px) {
  .loginForm {
    width: 100%;
  }

  .logo img {
    width: 110px;
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .bg-img {
    min-height: 500px;
  }

  .bg-img.login {
    min-height: 370px;
  }
}

.btn-register {
  color: #fff;
  background-color: #ce5a5a;
  border-color: #ce5a5a;
}

.btn-register:hover {
  text-decoration: none;
  color: #fff;
  background-color: #c63e3e;
}

.btn-login {
  color: #fff;
  background-color: #81dada;
  border-color: #81dada;
}

.btn-login:hover {
  text-decoration: none;
  color: #fff;
  background-color: #65d2d2;
}

.warning-bg {
  background-color: #ebbcbc;
  border-radius: 10px;
}

form {
  border-radius: 20px;
}




